
#login, #register{
    height: 100vh;
    background: url(/static/media/zanzibar.a6b8a1e0.jpg);
    background-position: center; 
    background-repeat: no-repeat; 
    background-size: cover; 
}

.auth-title-divide{
    border: 1px solid white;
    height: 5rem;
}

.text-box, .text-box:focus, .text-box:active{
    background: none;
    border-top: none;
    border-left: none;
    border-right: none;
    outline: none;
}

.main-container{
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);


    z-index: 1;
    margin: 0;

}
.img-overlay{
    position: absolute;
    background: rgba(0, 0, 0, 0.52);
    width: 100%;
    height: 100%;
    top: 0;
}

.auth-col{
    border: 1px solid transparent;
    background: #ffffff26;
    border-radius: 10px;
}
.auth-title > h3{
    font-weight: 400;
}
.icon-cancel-div{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    color: white;
}

.icon-cancel-div > .icon-cancel{
    position: absolute;
    z-index: 1;
    font-size: 2rem;
}

.location-text{
    position: absolute;
    z-index: 1;
    bottom: 2rem;
    right: 2rem;
    font-size: 13px;
    font-weight: bolder;
}

/* .form-check{
    position: absolute;
    z-index: 1;
} */

.checkbox-title{
    font-size: 12px;
}
#header-nav{
    display: -webkit-flex;
    display: flex;
    padding: 1px 2.8rem;
    height: 4.5rem;

}
 .logo{
    border-radius: 10%;
}
#header-nav > ul{
    display: -webkit-flex;
    display: flex;
    /* border: 1px solid red; */
    width: 13%;
    -webkit-justify-content: space-around;
    justify-content: space-around;
}

.btn-itin{
    width: 30%;
    border-radius: 20px;
    height: 2rem;
    font-size: 12px
}

.itin-container{
    border: 1px solid red;
    width: 85%;
}
