#login, #register{
    height: 100vh;
    background: url(../images/zanzibar.jpg);
    background-position: center; 
    background-repeat: no-repeat; 
    background-size: cover; 
}

.auth-title-divide{
    border: 1px solid white;
    height: 5rem;
}

.text-box, .text-box:focus, .text-box:active{
    background: none;
    border-top: none;
    border-left: none;
    border-right: none;
    outline: none;
}

.main-container{
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);


    z-index: 1;
    margin: 0;

}
.img-overlay{
    position: absolute;
    background: rgba(0, 0, 0, 0.52);
    width: 100%;
    height: 100%;
    top: 0;
}

.auth-col{
    border: 1px solid transparent;
    background: #ffffff26;
    border-radius: 10px;
}
.auth-title > h3{
    font-weight: 400;
}
.icon-cancel-div{
    display: flex;
    justify-content: flex-end;
    color: white;
}

.icon-cancel-div > .icon-cancel{
    position: absolute;
    z-index: 1;
    font-size: 2rem;
}

.location-text{
    position: absolute;
    z-index: 1;
    bottom: 2rem;
    right: 2rem;
    font-size: 13px;
    font-weight: bolder;
}

